//vuex，提供全局数据存储
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //数据项，相当于vue组件的data
  state:{
    //面包屑
    breadcrumb:"",
    zdList:[]
  },
  //存放取出state中数据的函数
  getters:{
    //面包屑
    getBreadCrumb:state => {
      return state.breadcrumb;
    },
    getZD:state =>{
      return state.zdList;
    }
  },
  //将vue组件中的数据赋值给state中的数据
  mutations:{
    //面包屑
    //此函数默认传两个参数，第一个是state对象，第二个是我们填入的数据
    setBreadCrumb:(state,obj) => {
      state.breadcrumb = obj;
    },
    setZD:(state, obj) => {
      state.zdList = obj;
    }
  }
})



